import React, { useEffect, useState } from "react";
import Axios from "axios";
import { Row, Col, Card, Button, Drawer } from "antd";
import ImageSlider from "../../utils/ImageSlider";
import CheckBox from "../LandingPage/Sections/CheckBox";
import RadioBox from "../LandingPage/Sections/RadioBox";
import SearchFeature from "../LandingPage/Sections/SearchFeature";
import {
  regions,
  price,
  difficulty,
  duration,
  elevation,
  totalelevation,
} from "../LandingPage/Sections/Datas";
// import "../LandingPage/Sections/Landing.css";
import { Carousel } from "antd";
import { ControlOutlined } from "@ant-design/icons";
import "./SearchPage.css";
import { useDispatch } from "react-redux";
import { addToCart } from "../../../_actions/user_actions";
import { useSelector } from "react-redux";
import PrivateTripsModal from "../../utils/PrivateTripsModal";
import { Link } from "react-router-dom";

const { Meta } = Card;

function SearchPage() {
  const user = useSelector((state) => state.user);

  const [Products, setProducts] = useState([]);
  const [Skip, setSkip] = useState(0);
  const [Limit, setLimit] = useState(6);
  const [PostSize, setPostSize] = useState();
  const [SearchTerms, setSearchTerms] = useState("");
  const [Filters, setFilters] = useState({
    regions: [],
    difficulty: [],
    duration: [],
    price: [],
    elevation: [],
    totalelevation: [],
  });
  const dispatch = useDispatch();
  const [showLoadMore, handleShowLoadMore] = useState(false);

  useEffect(() => {
    window.addEventListener("scroll", () => {
      if (window.innerHeight + window.scrollY >= document.body.scrollHeight) {
        handleShowLoadMore(true);
      } else handleShowLoadMore(false);
    });
    return () => {
      window.removeEventListener("scroll", SearchPage, true);
    };
  }, []);

  useEffect(() => {
    const variables = {
      skip: Skip,
      limit: Limit,
    };

    getProducts(variables);
  }, []);

  const getProducts = (variables) => {
    Axios.post("/api/product/getProducts", variables).then((response) => {
      if (response.data.success) {
        if (variables.loadMore) {
          setProducts([...Products, ...response.data.products]);
        } else {
          setProducts(response.data.products);
        }
        setPostSize(response.data.postSize);
      } else {
        alert("Failed to fectch product datas");
      }
    });
  };

  const onLoadMore = () => {
    let skip = Skip + Limit;

    const variables = {
      skip: skip,
      limit: Limit,
      loadMore: true,
      filters: Filters,
      searchTerm: SearchTerms,
    };
    getProducts(variables);
    setSkip(skip);
  };

  const renderCards = Products.map((product, index) => {
    const productId = product._id;

    const addToCartHandler = (productId) => {
      if (user.userData && !user.userData.isAuth) {
        return alert("Please Log in first");
      }

      dispatch(addToCart(productId));
      console.log(productId);
    };

    function formatNumber(num) {
      return num.toString().replace(/(\d)(?=(\d{3})+(?!\d))/g, "$1,");
    }

    return (
      <Col xs={24} key={index}>
        <Row
          style={{
            marginBottom: "1rem",
            border: "1px solid #ddd",
            boxShadow: "0 0 10px rgba(0,0,0,0.4)",
          }}
        >
          <Col
            xs={24}
            sm={0}
            style={{
              padding: "0 1rem",
            }}
          >
            <h2 className="xs_title">{product.title}</h2>
          </Col>
          <Col xs={24} sm={12} md={8}>
            <ImageSlider
              draggable={true}
              autoplay
              autoplaySpeed={5000}
              speed={1000}
              images={product.images}
            />
          </Col>
          <Col xs={0} sm={12} md={16} style={{ padding: "1rem" }}>
            <Row>
              <Col xs={24} md={12}>
                {" "}
                <h2>{product.title}</h2>
                <b>{product.location}</b>
                <br />
                <Col xs={0} md={24}>
                  <br />
                  <h4>Duration: {product.duration} day(s)</h4>
                  <h4>Difficulty: {product.difficulty}/10</h4>
                  <h4>
                    Elevation: {formatNumber(`${product.elevation}`)} meters
                  </h4>
                  <h4>
                    Climbing Height: {formatNumber(`${product.totalelevation}`)}{" "}
                    meters
                  </h4>
                </Col>
              </Col>
              <Col className="resultsDetails__right" xs={24} md={12}>
                {" "}
                <div>
                  {" "}
                  <Col xs={0} md={24}>
                    {" "}
                    <PrivateTripsModal />
                  </Col>
                </div>
                <div style={{ transform: "translateY(20px)" }}>
                  <h3>
                    NOW{" "}
                    <span style={{ fontSize: "2rem" }}>
                      {" "}
                      ₱{formatNumber(`${product.price}`)}{" "}
                    </span>
                    p.p
                  </h3>
                  <Button size="large" type="default" shape="round">
                    <Link to={`/product/${product._id}`}>LEARN MORE</Link>
                  </Button>
                </div>
              </Col>
            </Row>
          </Col>

          <Col xs={24} sm={0}>
            {" "}
            <div className="xs_avail">
              {" "}
              <Button size="middle" type="primary">
                <Link to={`/product/${product._id}`}>
                  <h3>LEARN MORE</h3>
                </Link>
              </Button>
              <span style={{ fontSize: "1.5rem", marginLeft: "auto" }}>
                ₱{product.price}
              </span>
            </div>
          </Col>
        </Row>
      </Col>
    );
  });

  const showFilteredResults = (filters) => {
    const variables = {
      skip: 0,
      limit: Limit,
      filters: filters,
    };
    getProducts(variables);
    setSkip(0);
  };

  const handlePrice = (value) => {
    const data = price;
    let array = [];

    for (let key in data) {
      if (data[key]._id === parseInt(value, 10)) {
        array = data[key].array;
      }
    }
    console.log("array", array);
    return array;
  };

  const handleElevation = (value) => {
    const data = elevation;
    let array = [];

    for (let key in data) {
      if (data[key]._id === parseInt(value, 10)) {
        array = data[key].array;
      }
    }
    console.log("array", array);
    return array;
  };

  const handleTotalElevation = (value) => {
    const data = totalelevation;
    let array = [];

    for (let key in data) {
      if (data[key]._id === parseInt(value, 10)) {
        array = data[key].array;
      }
    }
    console.log("array", array);
    return array;
  };

  const handleFilters = (filters, category) => {
    const newFilters = { ...Filters };

    newFilters[category] = filters;

    if (category === "price") {
      let priceValues = handlePrice(filters);
      newFilters[category] = priceValues;
    } else if (category === "elevation") {
      let elevationValues = handleElevation(filters);
      newFilters[category] = elevationValues;
    } else if (category === "totalelevation") {
      let totalelevationValues = handleTotalElevation(filters);
      newFilters[category] = totalelevationValues;
    } else {
    }

    console.log(newFilters);

    showFilteredResults(newFilters);
    setFilters(newFilters);
  };

  const updateSearchTerms = (newSearchTerm) => {
    const variables = {
      skip: 0,
      limit: Limit,
      filters: Filters,
      searchTerm: newSearchTerm,
    };

    setSkip(0);
    setSearchTerms(newSearchTerm);

    getProducts(variables);
  };

  const SearchFilter = (
    <Row gutter={[16, 16]}>
      <Col xs={24}>
        <CheckBox
          name={"Regions"}
          list={regions}
          handleFilters={(filters) => handleFilters(filters, "regions")}
        />
      </Col>
      <Col xs={24}>
        <CheckBox
          name={"Difficulty"}
          list={difficulty}
          handleFilters={(filters) => handleFilters(filters, "difficulty")}
        />
      </Col>
      <Col xs={24}>
        <CheckBox
          name={"Duration"}
          list={duration}
          handleFilters={(filters) => handleFilters(filters, "duration")}
        />
      </Col>
      <Col xs={24}>
        <RadioBox
          name={"Price"}
          list={price}
          handleFilters={(filters) => handleFilters(filters, "price")}
        />
      </Col>
      <Col xs={24}>
        <RadioBox
          name={"Elevation"}
          list={elevation}
          handleFilters={(filters) => handleFilters(filters, "elevation")}
        />
      </Col>
      <Col xs={24}>
        <RadioBox
          name={"Climbing Height"}
          list={totalelevation}
          handleFilters={(filters) => handleFilters(filters, "totalelevation")}
        />
      </Col>
    </Row>
  );

  const [visible, setVisible] = useState(false);

  const showDrawer = () => {
    setVisible(true);
  };

  const onClose = () => {
    setVisible(false);
  };

  return (
    <div>
      <div className="searchshowcase">
        <h1>HIKING DESTINATIONS</h1>
      </div>
      <section className="spikes"></section>
      <Row
        gutter={16}
        style={{
          maxWidth: "1200px",
          margin: "auto",
          marginBottom: "1rem",
          minHeight: "100vh",
        }}
      >
        <Col
          xs={24}
          lg={0}
          style={{
            background: "#fff",
            height: "fit-content",
            marginBottom: "2rem",
            borderRadius: "3px",
            position: "-webkit-sticky",
            position: "sticky",
            top: "0",
            zIndex: "1",
            padding: "0.5rem ",
          }}
        >
          <Row align="middle" className="filterXS">
            <Col xs={24} sm={12} style={{ margin: "0.5rem auto " }}>
              {" "}
              <SearchFeature refreshFunction={updateSearchTerms} />
            </Col>
            <Col xs={24} sm={12}>
              {" "}
              <Button
                size="large"
                type="primary"
                onClick={showDrawer}
                style={{
                  width: "100%",
                  fontSize: "1.5rem",
                  display: "flex",
                  alignItems: "center",
                  justifyContent: "center",
                }}
              >
                <ControlOutlined /> Filter
              </Button>
              <Drawer
                title="Filter your Results"
                placement="right"
                closable={true}
                onClose={onClose}
                visible={visible}
              >
                {SearchFilter}
              </Drawer>
            </Col>
          </Row>
        </Col>
        <Col
          xs={0}
          md={0}
          lg={6}
          style={{
            height: "fit-content",
            border: "1px solid #ddd",
            boxShadow: "0 0 10px rgba(0,0,0,0.4)",
          }}
        >
          <h2>Filter your Results</h2>

          <SearchFeature refreshFunction={updateSearchTerms} />
          <br />
          {SearchFilter}
        </Col>
        <Col md={24} lg={18}>
          {Products.length === 0 ? (
            <div>
              <h2>No post yet...</h2>
            </div>
          ) : (
            <div>{renderCards}</div>
          )}
          <br />
          <br />

          {PostSize >= Limit && (
            <div
              style={{
                display: "flex",
                justifyContent: "center",
              }}
            >
              <Button
                className={`${showLoadMore && "add"}`}
                type="primary"
                onClick={onLoadMore}
                style={{
                  position: "fixed",
                  bottom: "300px",
                  visibility: "hidden",
                  transition: "0.3s ease-out",
                  transform: "translateY(20px)",
                  opacity: "0",
                }}
              >
                Load More
              </Button>
            </div>
          )}
        </Col>
      </Row>
    </div>
  );
}

export default SearchPage;
